.navbar {
  box-shadow: 2px 0px 6px #707070;
  background: #fff !important;
}

.navbar-brand {
  font-weight: 700;
}
.navbar-nav {
  width: 100%;
}

body {
  overflow-x: hidden;
  font-family: Montserrat;
}

.idea__image {
  padding: 0;
  margin: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.idea_highlight {
  color: #107895;
}
.idea_submit {
  margin-right: 16px;
  font-size: 14px;
  font-weight: 600;
  /* color: #083045; */
  color: #f1f1f1;
  background: #107895;
  /* background: #4285f4; */
  padding: 8px 16px;
  border: 0;
  -webkit-transition: 0.3s all ease;
  transition: 0.3s all ease;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.23);
}
.idea_submit:hover {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.16), 0px 4px 8px rgba(0, 0, 0, 0.23);
}
.custom_back {
  background: #f1f3f4;
}

.idea_card {
  border: 0;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.23);
  padding-right: 16px;
  padding-left: 16px;
  margin-bottom: 16px;
}
.idea_title {
  font-weight: 700;
  color: #707070;
}
@media (max-width: 767px) {
  .idea__image {
    border-top-right-radius: 4px;
    /* border-bottom-left-radius: 0; */
  }
}
@media (max-width: 424px) {
  .idea_submit {
    margin-bottom: 16px;
  }
}

.leadercard {
    /* border: 2px solid #5576D1; */
    background: transparent;
}

h5{
    color: #444444;
}

.name {
    color: #444444;
    font-weight: 500;
    font-size: 0.8em;
}

.rank {
    font-size: 0.8em;
    color: #5576D1;
    font-weight: 500;
    padding-bottom: 0;
    margin-bottom: 0;
}

.score{
    color: #5576D1;
    font-weight: 600;
}

.level {
    color: #FF5696;
    font-weight: 600;
}
